body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar-title {
  text-transform: uppercase;
}
.full-screen-loader {
  position: fixed;
  top: calc(50% - 30px);
  left: calc(50% - 25px);  
}
.page__content {
  max-width: 475px;
  margin: auto;
}
ons-input, .input-wrapper {
    width: -webkit-fill-available;
}
.input-feedback-invalid {
    width: -webkit-fill-available;
    margin-top: .25rem;
    font-size: 80%;
    color: #dc3545;
}
[class*='adv-list-header'] {
    text-align: center;
    padding-top: 0.5rem;
    background: white;
    text-transform: none;
    font-size: 15px;
}
.checkbox--can-be-checked :checked + .checkbox__checkmark:before {
    background: #b6b6b6;
    border: none;
}
[class*='item-can-be-checked'] {
    color: #b6b6b6;
}

[class*='custom-form-footer'] {
    text-transform: none;
    font-weight: bold;
    padding-top: 1rem; 
}
[class*='progress-circular--send-btn-progress__primary'] {
    stroke: white;
}
[class*='custom-list-header'] {
    text-align: center;
    padding: 0.5rem;
}

[class*='custom-list-item'] {
    padding: 0.5rem;
    text-transform: uppercase;
}
[class*='custom-list-item']::before {
    content : "";
    position: absolute;
    left: 1rem;
    bottom: 0;
    height: 1px;
    width: calc(100% - 2rem);  /* or 100px */
    border-bottom:1px solid #ccc;
}
label[class*='custom-list-item'] {
    background-image: none
}
:checked + .checkbox__checkmark:before {
    background: #b5a05f;
    border: none;
}
[class*='item-selected'] {
    color: #b5a05f;
}

[class*='custom-list-footer'] {
    text-align: center;
    padding: 0.5rem;
    text-transform: none;
    font-weight: bold;
}

[class*='custom-btn'] {
    text-transform: uppercase;
    background-color: #b5a05f;
}
[class*='custom-btn']:active {
    background-color: #b5a05f;
    opacity: 1;
}

[class*='custom-back-btn'] {
    line-height: 44px;
    color: black; 
    fill: black;
}
